/* RESET E PADRÕES */

@import url('https://fonts.googleapis.com/css2?family=Spectral:wght@700&display=swap');

* {
    box-sizing: border-box;
}

body {
    margin: 0px;
    color: #333;
    --type-first: Helvetica, Arial, sans-serif;
    --type-second: 'Spectral', Georgia;
    font-family: var(--type-first);
    font-size: 1rem;
}

h1,
h2,
h3,
h4,
p {
    margin: 0px;
}

ul,
li {
    list-style: none;
    padding: 0px;
    margin: 0px;
}

img {
    display: block;
    max-width: 100%;
}

button,
input {
    display: block;
    font-size: 1rem;
    font-family: var(--type-first);
    color: #333;
}

a {
    text-decoration: none;
    color: #333;
}

:root {
    --primary-color: #2C8C2D;
    --primary-color-rgb: 44, 140, 45;
    --secondary-color: #98BF00;
    --secondary-color-rgb: 152, 191, 0;
    --terciary-color: #364742;
    --terciary-color-rgb: 54, 71, 66;
}

* {
    scrollbar-color: var(--primary-color) #ccc;
}

body * {
    scrollbar-width: thin;
}

::-webkit-scrollbar {
    width: 8px;
    background-color: #ccc;
}

body::-webkit-scrollbar {
    width: initial;
    scrollbar-color: var(--primary-color) #ccc;
    scrollbar-width: thin;
}

::-webkit-scrollbar-thumb,
::-webkit-scrollbar-thumb:hover,
::-webkit-scrollbar-thumb:active {
    background-color: var(--primary-color);
}

.default__title {
    font-family: var(--type-second);
    line-height: 1;
    font-size: 3rem;
    margin-left: 5px;
    position: relative;
    z-index: 1;
}

.default__title::after {
    content: '';
    display: block;
    width: 1.5rem;
    height: 1.5rem;
    background-color: var(--secondary-color);
    position: absolute;
    bottom: 5px;
    left: -5px;
    border-radius: .2rem;
    z-index: -1;
}

.default__btn {
    width: 100%;
    height: 40px;
    max-width: fit-content;
    line-height: 38px;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border-radius: 0.4rem;
    cursor: pointer;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-align-self: center;
    -ms-flex-item-align: center;
    align-self: center;
    font-size: 1rem;
    margin: 30px auto 0px;
    padding: 0px 15px;
    text-align: center;
    text-transform: uppercase;
    font-family: var(--type-first);
    font-weight: 700;
    border: 2px solid var(--primary-color);
    background-image: -webkit-linear-gradient(45deg, var(--primary-color) 50%, transparent 50%);
    background-image: linear-gradient(45deg, var(--primary-color) 50%, transparent 50%);
    background-position: 100%;
    background-size: 400%;
    -webkit-transition: background 300ms ease-in-out;
    transition: background 300ms ease-in-out, color .3s;
}

.default__btn:hover {
    background-position: 0;
    color: #FFF;
}

